import {connect} from "react-redux";
import {UserArray} from "./UserArray";
import {followOnUserThunkCreator, unFollowOnUserThunkCreator} from "../../../store/reducers/usersReducer";
import {getIsFetchingUsers, getPage} from "../../../store/reselect/reselect";

const mapStateToProps = state => {
    return {
        page: getPage(state),
        isFetching: getIsFetchingUsers(state)
    };
};

const mapDispatchToProps = dispatch => {
    return {
        followOnUser: (userId, page) => {
            dispatch(followOnUserThunkCreator(userId, page));
        },
        unFollowOnUser: (userId, page) => {
            dispatch(unFollowOnUserThunkCreator(userId, page));
        }
    };
};

export const UserContainer = connect(mapStateToProps, mapDispatchToProps)(UserArray)