import React, {useEffect} from "react";
import {Pagination} from "antd";
import {UserContainer} from "./User/UserContainer";
import {Loading} from "../Loading/Loading";

export const Users = ({getUsers, totalUserCount, pageSize, usersList, page, isFetching}) => {
    const totalPage = Math.ceil(totalUserCount / pageSize);

    useEffect(() => {
        getUsers(page);
    }, []);

    if (isFetching) {
        return <Loading/>
    };

    return (
        <div>
            <Pagination
                showSizeChanger={false}
                onChange={current => getUsers(current)}
                defaultCurrent={pageSize}
                total={totalPage}
            />
            <div style={styles.usersWrap}>
                {usersList ? <UserContainer usersList={usersList}/> : null}
            </div>
        </div>
    );
};

const styles = {
    usersWrap: {
        padding: "50px 0"
    }
};