import React from "react";
import {User} from "./User";

export const UserArray = ({usersList, followOnUser, unFollowOnUser, page, isFetching}) => {
    const usersArray = usersList.map(item => {
        return (
            <User
                unFollowOnUser={unFollowOnUser}
                isFetching={isFetching}
                followOnUser={followOnUser}
                page={page}
                user={item}
                key={item.id}
            />
        );
    });

    return (
        <div>
            {usersArray}
        </div>
    );
};