import React from "react";
import {Button} from "antd";

export const UserButton = ({isFollowed, userId, followOnUser, page, unFollowOnUser}) => {
    if (isFollowed) {
        return <Button onClick={() => unFollowOnUser(userId, page)}>Un follow</Button>
    };

    return <Button onClick={() => followOnUser(userId, page)}>Follow</Button>
};