import React from "react";
import {Image} from "antd";
import {UserButton} from "./UserButton/UserButton";
import {Loading} from "../../Loading/Loading";
import {NavLink} from "react-router-dom";

export const User = ({user, followOnUser, unFollowOnUser, page, isFetching}) => {
    return (
        <div style={styles.user}>
            <NavLink to={`/profile/${user.id}`}>
                <Image src={user.photos.small || "https://bit.ly/3sQ23rN"} width={100}/>
            </NavLink>
            <div style={styles.userInfo}>
                <div style={styles.nameWrap}>
                    <p style={styles.text}>Name: {user.name}</p>
                </div>
                <div style={styles.statusWrap}>
                    <p style={styles.text}>Status: {user.status || "Неизвестно"}</p>
                </div>
            </div>
            <div>
                {
                    isFetching
                        ? <Loading style={{textAlign: "left"}}/>
                        : <UserButton
                            page={page}
                            unFollowOnUser={unFollowOnUser}
                            followOnUser={followOnUser}
                            userId={user.id}
                            isFollowed={user.followed}
                        />
                }
            </div>
        </div>
    );
};

const styles = {
    user: {
        padding: 20,
        marginBottom: 20
    },
    userInfo: {
        display: "grid",
        padding: "20px 0",
        gridTemplateColumns: "repeat(2, 400px)",
        justifyContent: "space-between"
    },
    nameWrap: {
        paddingBottom: 10,
    },
    statusWrap: {
        justifySelf: "end",
        paddingBottom: 10,
    },
    text: {
        fontSize: 20,
        letterSpacing: "1px"
    },
};