import {connect} from "react-redux";
import {Users} from "./Users";
import {getUsersThunkCreator} from "../../store/reducers/usersReducer";
import {
    getIsFetchingUsersForChangePageUser,
    getPage,
    getPageSize,
    getTotalUserCount,
    getUsersList
} from "../../store/reselect/reselect";

const mapStateToProps = state => {
    return {
        page: getPage(state),
        pageSize: getPageSize(state),
        totalUserCount: getTotalUserCount(state),
        usersList: getUsersList(state),
        isFetching: getIsFetchingUsersForChangePageUser(state)
    };
};

const mapDispatchToProps = dispatch => {
    return {
        getUsers: page => {
            dispatch(getUsersThunkCreator(page));
        }
    };
};

const UsersContainer = connect(mapStateToProps, mapDispatchToProps)(Users);

export default UsersContainer;
